import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Injectable({
	providedIn: 'root'
})
export class HttpService {
	constructor(private _http: HttpClient) {
		this.getAll;
	}

	getAll() {
		let temp = this._http.get('/api/tasks');
		temp.subscribe((data) => console.log('Got our data', data));
	}

	getOne() {
		let temp = this._http.get('/api/tasks/:id');
		temp.subscribe((data) => console.log('Got it', data));
	}
}
